@use '../../../variables' as var;

.SwiperControl {
  position: relative;
  width: 100%;
  max-width: 400px;
  margin: 25px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 35px;
}
.arrow {
  width: 35px;
  cursor: pointer;
  &.reverse {
    transform: scaleX(-1);
  }
}
.progress {
  width: 100%;
  flex: 1;
}
.progress {
  -webkit-appearance: none; /* Override default CSS styles */
  appearance: none;
  height: 10px; /* Specified height */
  background: white;
  border-radius: 10px;
  overflow: visible;

  &::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    width: 27px; /* Set a specific slider handle width */
    height: 27px; /* Slider handle height */
    background: var.$color-primary;
    border-radius: 20px;
    border: 6px solid black;
  }
  &::-moz-range-thumb {
    appearance: none;
    width: 27px; /* Set a specific slider handle width */
    height: 27px; /* Slider handle height */
    border-radius: 20px;
    background: var.$color-primary;
    border: 6px solid black;
  }
}
