@use '../../../variables' as var;

.DrawResults {
  width: 100%;
  position: relative;
  user-select: none;
}
.resultTitle {
  margin-bottom: 30px;
}
.slide {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: var.$border-radius;
  background: var.$color-primary;
  backdrop-filter: blur(12px);
  // padding: 0 20px 20px 20px;
  overflow: hidden;
  gap: 15px;
  padding-bottom: 25px;
}
.slideHeaderWrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slideHeader {
  align-self: flex-start;
  background: linear-gradient(102.16deg, #59d89d 0%, #306a7c 100%);
  // padding: 20px;
  border-bottom-right-radius: 30px;
  // min-width: 35%;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  /* identical to box height, or 32px */
  color: black;
  padding: 10px 25px;
}
.rewardTotal {
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: 700;
  font-size: 14px;
  /* identical to box height, or 32px */
  color: black;
  padding-right: 25px;
  img {
    width: 25px;
  }
}

.result {
  width: 100%;
  padding: 0 25px 0 25px;
  display: flex;
  flex-direction: column;
  max-height: 350px;
  overflow-y: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */

  &::-webkit-scrollbar {
    display: none;
  }
}
.resultItem {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;

  h2,
  p {
    font-weight: 700;
    font-size: 14px;
    margin: 3px 0;
  }
  h2 {
    color: black;
  }
}
.title {
  text-align: left;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  /* identical to box height, or 16px */
  color: #ffffff;
  align-self: flex-start;
  margin-left: 25px;
}

