@use '../../variables' as var;

.StatsItem {
  width: 100%;
  border: 2px solid rgba(84, 84, 84, 0.1);
  background-color: white;
  border-radius: var.$border-radius;
  display: flex;
  overflow: hidden;
}
.statsIconWrap {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  img {
    width: 30px;
  }
}
.statsTextWrap {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
  h2 {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    /* identical to box height, or 18px */
    text-align: center;
    color: black;
  }
  p {
    font-weight: 700;
    font-size: 22px;
    line-height: 100%;
    text-align: center;
    color: var.$color-primary;
  }
}
