@use '../../../../variables' as var;

$card-padding: 10px;
$card-border-radius: var.$border-radius;

.FlipClockCard {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  gap: 5px;
}

.title {
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  text-align: center;
  color: black;
}

.card {
  position: relative;
  font-size: 72px;
  font-weight: 700;
  line-height: 100%;
  font-family: 'Roboto', sans-serif;
  perspective: 300px;
  width: fit-content;
  height: fit-content;
}

.background {
  background: black;
  border-radius: $card-border-radius;
  padding: $card-padding;
}

.flipper {
  position: absolute;
  top: 0;
  width: 100%;
  height: 50%;
  transform-origin: bottom;
  transform-style: preserve-3d;
  &.flipped {
    transform: rotateX(-180deg);
    transition: all 0.5s ease-in;
  }
  .front,
  .back {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
    background: black;
  }
  .front {
    border-top-left-radius: $card-border-radius;
    border-top-right-radius: $card-border-radius;
    padding: $card-padding $card-padding 0 $card-padding;
  }
  .back {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    transform: rotateX(180deg);
    border-bottom-left-radius: $card-border-radius;
    border-bottom-right-radius: $card-border-radius;
    padding: 0 $card-padding $card-padding $card-padding;
  }
}
.bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  overflow: hidden;
  background: black;
  border-bottom-left-radius: $card-border-radius;
  border-bottom-right-radius: $card-border-radius;
  padding: 0 $card-padding $card-padding $card-padding;
}
.divider {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: none;
  margin: 0;
  border-top: 3px solid var.$color-primary;
}

@media (max-width: var.$mobile-break-point) {
  .title {
    font-size: 16px;
  }

  .card {
    font-size: 56px;
  }
}
